<template>
  <v-container class="desktop_margin pb-15 create_orderform_container">
    <v-overlay :value="set_overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <div class="desktopView">
      <div
        class="header-text"
        v-if="!form_saved"
        v-show="!$store.state.overlay"
      >
        {{ $t("newUi.forms.createFormNew", { form: headerName }) }}
      </div>
      <div class="header-text" v-if="form_saved">{{ payload.shopname }}</div>
    </div>
    <div v-if="payload.type == 'Ordering form'">
      <div
        v-if="!form_saved"
        class="desktopView background_color"
        style="padding: 20px"
      >
        {{ $t("newUi.adminPanel.infoToolTip4") }}
      </div>
    </div>

    <div v-if="payload.type != 'Ordering form'">
      <div
        v-if="!form_saved"
        class="desktopView background_color"
        style="padding: 20px"
      >
        {{ $t("newUi.adminPanel.infoToolTip5") }}
      </div>
    </div>
    <v-container
      v-if="!form_saved"
      class="section_form_middle padding_view"
      :style="$vuetify.breakpoint.height < 450 ? { 'margin-top': '20px' } : {}"
    >
      <v-container
        style="max-width: 550px"
        class="section_desktop padding_view"
      >
        <div v-if="payload.type == 'Ordering form'">
          <div class="text-center text_color mobileview">
            {{ $t("newUi.adminPanel.infoToolTip4") }}
          </div>
        </div>
        <div v-if="payload.type != 'Ordering form'">
          <div class="text-center text_color mobileview">
            {{ $t("newUi.adminPanel.infoToolTip5") }}
          </div>
        </div>
        <v-text-field
          v-bind:label="$t('newUi.editForm.addName')"
          v-model="shop_name"
          placeholder=""
          autocomplete="off"
          v-bind:title="$t('')"
          required
          maxlength="100"
        ></v-text-field>
        <v-textarea
          v-bind:label="$t('newUi.editForm.editDesc')"
          v-model="shopdesc"
          autocomplete="off"
          required
          placeholder=""
          name="text"
          row="2"
          row-height="20"
        ></v-textarea>
        <v-select
          v-model="currency"
          style="padding-top:22px"
          :items="this.items"
          v-bind:label="this.placeHolderText()"
        ></v-select>
        <label class="whatsappText">
          {{ $t("newUi.editForm.whatsAppDesc") }}
        </label>
        <vue-tel-input
          :autoFormat="false"
          :showDialCode="true"
          :validCharactersOnly="true"
          v-model="mobileno"
          :autoDefaultCountry="true"
          :dropdownOptions="{
            showFlags: true,
            showDialCodeInList: true,
            showDialCodeInSelection: true,
          }"
          :inputOptions="{
            placeholder: $t('newUi.createOrderForm.phoneNumber'),
            maxlength: 16,
          }"
          @input="onInput"
          @country-changed="countryChanged"
          inputOptions.placeholder=""
          mode="international"
          id="create_for_mobile"
          @validate="onValidate"
          :enabledCountryCode="true"
          inputId="create_for_mobile"
        ></vue-tel-input>
        <div class="text-center mt-10">
          <div class="section_desktop desktopView">
            <v-btn @click="cancel" class="button_padding cancel_button">
              {{ $t("newUi.editForm.cancel") }}
            </v-btn>
            <v-btn
              @click.prevent="submit"
              class="button_padding submit_button"
              >{{ $t("newUi.createOrderForm.submit") }}</v-btn
            >
          </div>
          <div class="mobileview bottom_tab">
            <v-btn @click="cancel" class="button_padding cancel_button">
              {{ $t("newUi.editForm.cancel") }}
            </v-btn>
            <v-btn
              @click.prevent="submit"
              class="button_padding submit_button"
              >{{ $t("newUi.createOrderForm.submit") }}</v-btn
            >
          </div>
        </div>
      </v-container>
    </v-container>
    <SuccessOrderForm v-if="form_saved" :payload="payload"></SuccessOrderForm>
  </v-container>
</template>
<script>
import { saveFile, setLocale, analytics } from "../services/firebase";
import { saveMasterData } from "../services/firebase";
import store from "../store/store";
import i18n from "../i18n";
import router from "../router/router";
import SuccessOrderForm from "./SuccessOrderForm.vue";
import Currencies from "../assets/currency";
export default {
  name: "GsCatelogueEditForm",
  props: ["payload", "headerName"],
  components: {
    SuccessOrderForm,
  },
  data() {
    return {
      mobileno: "",
      validateNumber: "hide",
      form_saved: false,
      phone: "",
      new_number: "",
      currency: "",
      shop_name: "",
      shopdesc: "",
      phoneNumber: "",
      data: "",
      national_number: "",
      valid_number: "",
      shop_phone_number: "",
      phone_valid: "",
      set_overlay: false,
      items: [],
    };
  },
  watch: {
    $route: "clearing_data",
  },
  computed: {
    isValid: function() {
      return this.mobileno === "" ? true : this.valid;
    },
  },
  mounted() {
    setLocale();
    for (const currency in Currencies) {
      var symbol = Currencies[currency];
      this.items.push(currency + " - " + symbol["symbol"]);
    }
  },
  methods: {
    clearing_data() {
      this.shop_name = "";
      this.shopdesc = "";
      this.currency = "";
      this.mobileno = "";
    },
    placeHolderText: function() {
      return i18n.t("newUi.adminViewform.currency");
    },
    addcateg: function() {
      var p = this.$store.state.currentForm.catalogue.maxlength;
      if (!p) {
        p = 1;
        this.$store.dispatch("setMaxLength", 1);
      } else {
        var maxlength = this.$store.state.currentForm.catalogue.maxlength + 1;
        p = maxlength;
        this.$store.dispatch("setMaxLength", maxlength);
      }
      this.$store.state.currentForm.catalogue.push({
        pos: p,
        type: "category",
        name: "",
      });
    },
    countryChanged(formattedNumber) {
      this.data = formattedNumber;
    },
    onInput(formattedNumber, { number, valid }) {
      // debugger;
      this.phone_valid = valid;
      this.new_number = number;
      this.national_number = formattedNumber;
    },
    onValidate: function({ valid }) {
      this.phone_valid = valid;
      // if(valid == true) {
      //     this.new_number = number;
      // }
    },
    additem: function() {
      var p = this.$store.state.currentForm.catalogue.maxlength;
      if (!p) {
        p = 1;
        this.$store.dispatch("setMaxLength", 1);
      } else {
        var maxlength = this.$store.state.currentForm.catalogue.maxlength + 1;
        p = maxlength;
        this.$store.dispatch("setMaxLength", maxlength);
      }
      this.$store.state.currentForm.catalogue.push({
        pos: p,
        type: "item",
        name: "",
        desc: "",
        imageurl: "",
        price: 0,
        qtty: 0,
      });
    },
    delitem: function() {
      this.$store.state.currentForm.catalogue.pop();
    },
    submit: function() {
      var exists = false;
      if (!this.shop_name || this.shop_name == 0 || this.shop_name == "") {
        alert(i18n.t("alertText.nameAlert"));
      } else if (!this.shopdesc || this.shopdesc == 0 || this.shopdesc == "") {
        alert(i18n.t("admin.editForm.label.placeholder3"));
      } else if (this.currency != "" && !this.currency.includes("undefined")) {
        this.payload.phone = this.new_number;
        this.payload.nationalNumber = this.national_number;
        this.$store.state.forms.map((element) => {
          if (
            element.shopname.toLowerCase().replace(/ /g, "") ===
            this.shop_name.toLowerCase().replace(/ /g, "")
          ) {
            exists = true;
          }
        });
        if (exists) alert(i18n.t("alertText.validateUnque"));
        else this.saveCurrentForm();
      } else {
        alert(i18n.t("alertText.fillCurrency"));
      }
    },
    cancel: function() {
      router.push("/admin");
    },
    validatePhone: function(phone) {
      var regex = /^\+(?:[0-9] ?){6,14}[0-9]$/;
      if (regex.test(phone)) {
        //this.validateNumber = "hide";
        return true;
      } else {
        //this.validateNumber = "display";
        return false;
      }
    },
    validatePrice: function() {
      var catelogue = this.$store.state.currentForm.catalogue;
      var data = {
        status: true,
        price: "",
      };
      if (catelogue.length > 0) {
        for (var i = 0; i < catelogue.length; i++) {
          if (catelogue[i].type == "item") {
            if (
              catelogue[i].price == "" ||
              parseFloat(catelogue[i].price) < 0 ||
              !parseFloat(catelogue[i].price)
            ) {
              data.status = false;
              data.price = catelogue[i].price;
              return data.status;
            }
          }
        }
        return data.status;
      } else {
        return data.status;
      }
    },
    formNameAnalytics: function(data) {
      var formType = this.$store.state.currentForm.type;
      if (formType == "Ordering form") {
        // console.log("order_forms_created", data)
        analytics.logEvent("order_forms_created", {
          formName: data.name,
          userId: data.userId,
        });
      } else {
        // console.log("catalogue_forms_created", data)
        analytics.logEvent("catalogue_forms_created", {
          formName: data.name,
          userId: data.userId,
        });
      }
    },
    saveCurrentForm: function() {
      this.payload.shopname = this.shop_name;
      this.payload.shopdesc = this.shopdesc;
      var shopname = this.$store.state.currentForm.shopname;
      var phone = this.$store.state.currentForm.phone;
      this.$store.state.currentForm.currency = this.currency
        ? this.currency.split("-")[0].trim()
        : "";
      this.$store.state.currentForm.symbol = this.currency
        ? this.currency.split("-")[1].trim()
        : "";
      this.$store.state.currentForm.createdOn = new Date();
      if (!shopname || shopname == 0 || shopname == "") {
        alert(i18n.t("alertText.nameAlert"));
      } else if (shopname.length > 25) {
        alert(i18n.t("alertText.formMax"));
      } else if (phone == "+91" || this.phone_valid != true) {
        alert(i18n.t("alertText.validatePhoneAlert"));
      } else {
        this.$store.dispatch("setOverlay", true);
        this.set_overlay = true;
        var elethis = this;
        var eventData = {
          name: shopname,
          userId: elethis.$store.state.currentForm.id,
        };
        elethis.formNameAnalytics(eventData);
        saveFile(
          this.$store.state.profile.uid,
          this.$store.state.currentFormPos,
          JSON.stringify(this.$store.state.currentForm),
          function(error) {
            if (error) {
              //TODO skeleton data
              alert(i18n.t("alertText.saveFailed"));
              store.dispatch("setOverlay", false);
              elethis.set_overlay = false;
            } else {
              // alert(i18n.t("alertText.uploadText"));
              store.dispatch("setOverlay", false);
              elethis.set_overlay = false;
            }
          }
        );
        saveMasterData(function(err) {
          console.log(err);
        }, this.$store.state);
        this.form_saved = true;
      }
    },
  },
};
</script>
<style lang="scss">
.button_padding {
  margin-right: 10px;
  font-weight: bold;
  padding: 10px;
  color: white !important;
  font-size: 12px;
  letter-spacing: 0px;
  text-align: center;
  cursor: pointer;
  text-transform: none;
}
.cancel_button {
  background-color: rgb(233, 80, 91) !important;
  border-color: rgb(233, 80, 91) !important;
}
.submit_button {
  background-color: rgb(18, 140, 126) !important;
  border-color: rgb(18, 140, 126) !important;
}
input[type="text"] {
  margin-bottom: 0px;
  border: none;
  margin-top: 0px;
}
.bottom_modal {
  border-top-right-radius: 20px !important;
  border-top-left-radius: 20px !important;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.bottom_tab {
  width: 100%;
  text-align: center;
  left: 0;
  right: 0;
  margin-top: 130px;
}
.theme--light.v-label {
  padding: 5px 0 !important;
  top: -1px;
  height: 30px;
}
.invalid_color {
  border: 1px solid red;
}
.theme--light.v-input input {
  color: #8ab26d;
}
.vue-tel-input {
  border: none;
}
.vti__dropdown {
  background-color: #f8faf7;
}
.vti__input {
  margin-left: 5px;
  background-color: #f8faf7;
  color: #8ab26d;
}
@media (min-width: 320px) and (max-width: 960px) {
  .section_form_middle {
    display: flex;
    margin-top: 35%;
    justify-content: center;
  }
  .whatsappText {
    text-align: left;
    font-size: 13px;
    letter-spacing: 0px;
    color: #7c7c7c;
    opacity: 1;
  }
  .section_middle {
    position: fixed;
    bottom: 5%;
  }
  .background_color {
    background-color: #f8faf7;
    padding: 10px;
    margin-bottom: 10px;
    width: 100%;
  }
}
@media (min-width: 960px) {
  .desktop_margin {
    padding: 0rem 4rem;
  }
  .section_desktop {
    text-align: start;
    margin-left: 0px !important;
  }
  .section_form_middle {
    display: flex;
    margin-top: 40px;
  }
  .background_color {
    background: #f4f4f4 0% 0% no-repeat padding-box;
    opacity: 1;
    font-size: 12px;
    letter-spacing: 0px;
    color: #666666;
    opacity: 1;
    padding: 20px;
  }
  .whatsappText {
    text-align: left;
    font-size: 13px;
    letter-spacing: 0px;
    color: #7c7c7c;
    opacity: 1;
  }
  .header-text {
    font-size: 24px;
    font-weight: bold;
    letter-spacing: 0px;
    color: #31333e;
    opacity: 1;
  }
}
.text_color {
  color: #8ab26d;
  font-weight: bold;
}

@media (max-width: 767px) {
  .create_orderform_container .padding_view {
    margin-top: 0 !important;
  }

  .v-application .create_orderform_container {
    padding: 12px !important;
  }
}

.vue-tel-input ul.vti__dropdown-list {
  padding-left: 0;
}

.vue-tel-input ul.vti__dropdown-list li {
  padding-left: 7px;
  padding-right: 7px;
}
</style>
